<template>
  <div>
    <header class="header">
      <div class="title-container">
        <span class="title">ShandowSocks </span>
      </div>
    </header>
    <div class="qrcode">
      <p style="font-size: 12px">关注微信公众号及时获取最新消息</p>
      <img height="100%" width="100%" src="../assets/getqrcode.jpeg"/>
    </div>
    <div class="list">
      <el-table
      :data="iplist"
      :height="height"
      border
      style="width: 100%;">
        <el-table-column
          prop="remarks"
          label="名称">
        </el-table-column>
        <el-table-column
          prop="server"
          label="IP地址">
        </el-table-column>
        <el-table-column
          prop="server_port"
          label="端口">
        </el-table-column>
        <el-table-column
          prop="password"
          label="密码">
        </el-table-column>
            <el-table-column
          prop="method"
          label="方法">
        </el-table-column>
        <el-table-column
          prop="time"
          label="更新时间">
        </el-table-column>
      </el-table>
    </div>
    <div class="download">
      <a href="http://localhost:3000/download" type="download">
        <i class="el-icon-download"></i>
      </a>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'IPList',
  data() {
    return {
      iplist: [],
      height: 600,
    };
  },
  created() {
    this.gData();
  },
  methods: {
    gData() {
      axios.get('http://localhost:3000/data').then((res) => {
        this.iplist = res.data;
      });
    },
  },
};
</script>

<style>
.list {
  max-width: 80%;
  margin: 0 auto;
  padding-top: 60px;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background: #000;
  z-index: 2;
}
.title-container {
  padding: 0 10%;
}
.title {
  color: #FFF;
  font-size: 24px;
  line-height: 60px;
}
.el-icon-download {
  color: #fff;
  font-size: 36px;
}
.download {
  width: 60px;
  height: 60px;
  position: absolute;
  background: #000;
  right: 5%;
  bottom: 5%;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.download:hover {
  background: #554d4d;
}
.qrcode {
  width: 100px;
  height: 100px;
  position: absolute;
  z-index: 3;
  right: 20px;
  top: 70px;
}
</style>




